// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const dateDefaultMonth = new Date(2016, 6, 1);
const minDate = new Date(2016, 6, 5);
const maxDate = new Date(2016, 6, 24);

const Demo = function () {
  return (
    <div>
      <h2>MinDate Or MaxDate To Select</h2>
      <Datepicker
        placeholder={'点击选择日期'}
        initMonth={dateDefaultMonth}
        minDate={minDate}
        maxDate={maxDate}
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo6')
);
